.uploader {

    .top-bar {
        display: flex;

        .guilds-menu {
            float: none;
        }
        .save-btn {
            margin-left: 130px;
            font-size: 16pt;
            background-color: #39ff39;
            color: black;
            text-align: center;
            padding: 5px;
        }

        .save-btn.disabled {
            pointer-events: none;
            opacity: 0.2;
        }
    }
    .drop-files-here {
        display: flex;
        width: 200px;
        height: 200px;
        background-color: #80808029;
        span {
            margin: auto;
        }
    }

    .tags {
        width: unset;
        padding-top: unset;
        display: flex;
        flex: 1;
        .tag-option  {
            background-color: white;
            color: black;
            font-size: 16pt;
            margin-right: 5px;
            padding: 5px;
            line-height: 40px;
            cursor: pointer;
        }

        input:active {
            outline: none !important;
        };

        .tag-sample-input {
            background-color: black;
            font-size: 16pt;
            color: white;
            flex: 1;
            width: 100%;
        }
    }

    .sample-option {
        .sample-name {
            font-size: 12pt;
            .checkmark {
                font-size: 14pt;
                color: #39ff39;
                margin-left: 10px;
            }
        }

        .wave-and-thumb {
            display: flex;

            .record-info {
                display: flex;
                flex: 1;
                .others {
                    margin-left: 10px;
                    .Dropdown-control {
                        border: 1px solid black;
                        width: 200px;
                        font-size: 9pt;
                        max-height: 60px;
                    }
                    .Dropdown-option {
                        font-size: 8pt;
                        width: 300px;
                    }
                }
                .record-meta {
                    margin-left: 5px;
                    width: 150px;
                    max-width: 150px;
                    overflow: hidden;
                    white-space: nowrap;
                    font-size: 8pt;
                    display: flex;
                    flex-direction: column;
                    .genres {
                        span {
                            margin-left: 3px;
                            background-color: white;
                            color: black;
                        }
                    }
                }
                             
                img {
                    width: 60px;
                    height: 60px;
                }

           }
        }
    }
}
